import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import { Table } from '../../../components';
import { getAllChefPayoutListSaga } from '../../../store/actions';
import PayModal from './PayModal';

// eslint-disable-next-line no-unused-vars
const Payout = props => {
  const [mounted, setMounted] = useState(false);
  const [modelOpen, setModelOpen] = useState(false);
  const [payoutId, setPayoutId] = useState('');
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const [tableFilter, setTableFilter] = useState({});
  const setTableFilters = (columnName, orderBy, search, skip, limit) => {
    tableFilter.columnName = columnName;
    tableFilter.orderBy = orderBy;
    tableFilter.search = search;
    tableFilter.skip = skip;
    tableFilter.limit = limit;
    setTableFilter(tableFilter);
  };
  const {
    chefPayoutList: { list: allData, totalRecords },
    totalPending,
    totalPaid,
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.payment);

  const dispatch = useDispatch();

  const getData = (columnName, orderBy, search, skip, limit) => {
    setTableFilters(columnName, orderBy, search, skip, limit);
    if (search && skip !== 0) {
      dispatch(
        getAllChefPayoutListSaga({
          columnName,
          orderBy,
          search,
          skip: 0,
          limit,
        }),
      );
    } else {
      dispatch(
        getAllChefPayoutListSaga({ columnName, orderBy, search, skip, limit }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('name', 'asc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  const openPayoutModal = data => {
    setPayoutId(data.payoutId);
    setModelOpen(true);
  };

  const tableFields = [
    {
      displayName: 'Vendor Name',
      key: 'restaurantName',
    },
    {
      displayName: 'Full Name',
      key: 'name',
    },
    {
      displayName: 'Email',
      key: 'email',
    },
    {
      displayName: 'Phone Number',
      key: 'phone',
    },
    {
      displayName: 'Payout',
      key: 'payouts',
    },
    {
      displayName: 'Transaction Id',
      key: 'transactionId',
    },
    {
      displayName: 'Status',
      key: 'isPaid',
      isConditional: true,
      positiveValue: 'Paid',
      negativeValue: 'Pending',
    },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-eye" aria-hidden="true" />,
          action: '/vendors/payout',
        },
        {
          view: <i className="fa fa-money" aria-hidden="true" />,
          action: openPayoutModal,
          conditionalKey: 'isPaid',
          conditionalValue: true,
        },
      ],
    },
  ];

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Payout (Current 15 days)</h2>
              </div>
              <div className="colours_action">
                <ul className="animated zoomIn">
                  <li>
                    <div className="icons-circle-number dark-green">
                      {totalPending + totalPaid}
                    </div>
                    <h4>Total Payable</h4>
                  </li>
                  <li>
                    <div className="icons-circle-number red">
                      {totalPending}
                    </div>
                    <h4>Total Pending</h4>
                  </li>
                  <li>
                    <div className="icons-circle-number violet">
                      {totalPaid}
                    </div>
                    <h4>Total Paid</h4>
                  </li>
                </ul>
              </div>

              <div className="coman-table" id="owner-detail-btn">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  linkPropsName="_id"
                  setSearchFlag={searchFlagSetter}
                  errorMsg={errorMsg || 'Loading...'}
                  // pageSizeProp={pageSize}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      {modelOpen && (
        <PayModal
          setModelOpen={setModelOpen}
          tableFilter={tableFilter}
          payoutId={payoutId}
        />
      )}
    </div>
  );
};

export default SetTokenHeader(Payout, axios);
